"use server";

import { z } from 'zod';

const PriceSchema = z.object({
    Symbol: z.string(),
    Price: z.number(),
});

async function getWalPrice(): Promise<number> {
    try {
        const response = await fetch(`https://api.diadata.org/v1/assetQuotation/Sui/0x557158348aEE7d4B651326c92fabB881b53941dC`);
        if (!response.ok) {
            throw new Error(`Failed to fetch price for WAL`);
        }
        const data = await response.json();
        const parsedData = PriceSchema.parse(data);
        return parsedData.Price;
    } catch (error) {
        console.error(`Error fetching price for WAL:`, error);
        throw error;
    }
}

async function getSuiPrice(): Promise<number> {
    try {
        const response = await fetch(`https://api.diadata.org/v1/assetQuotation/Sui/0x2::sui::SUI`);
        if (!response.ok) {
            throw new Error(`Failed to fetch price for SUI`);
        }
        const data = await response.json();
        const parsedData = PriceSchema.parse(data);
        return parsedData.Price;
    } catch (error) {
        console.error(`Error fetching price for SUI:`, error);
        throw error;
    }
}

export async function getPrices(): Promise<{ wal: number; sui: number }> {
    try {
        const [walPrice, suiPrice] = await Promise.all([
            getWalPrice(),
            getSuiPrice(),
        ]);

        return {
            wal: walPrice,
            sui: suiPrice,
        };
    } catch (error) {
        console.error("Failed to fetch prices:", error);
        return { wal: 0, sui: 0 };
    }
}
